// ======== BASE ========

body {
  margin: 0;
  box-sizing: border-box;
  font-family: 'Roboto', sans-serif;
  font-size: 14px;
  color: #262626;
  background-color: #FFF;
  text-align: center; }

// ========= TYPOGRAPHY ===========

// kosong melompong

// ====================================

.container {
  max-width: 1000px;
  margin: 0 auto; }

.responsive-table {
  overflow-x: auto; }

header {
  margin-bottom: 30px;
  img {
    max-width: 200px; }
  h1, h2 {
    font-weight: normal; }
  h1 {
    margin: 10px 0; }
  h2 {
    margin: 10px 0; } }
#search {
  position: relative;
  max-width: 15%;
  margin: 0 auto;
  input[type="text"] {
    width: 100%;
    border-radius: 2px;
    border: 1px solid rgba(0,0,0, 0.3);
    padding: 5px;
    border: 0;
    border-bottom: 2px solid grey;
    background: none;
    font-size: 14px;
    transition: border 0.5s;
    &:focus {
      outline: none;
      border-bottom: 2px solid darken(#26c6da, 10%); } } }
#magnifier {
  fill: grey;
  position: absolute;
  top: -10px;
  right: -10px;
  max-width: 20px;
  cursor: pointer; }

table {
  width: 100%;
  border-collapse: collapse;
  box-shadow: 0px 5px 5px rgba(0,0,0, 0.5); }
thead tr {
  background-color: #26c6da; }
th, td {
  padding: 15px; }
.item {
  border-bottom: 1px solid rgba(0,0,0, 0.1);
  cursor: pointer;
  transition: background 0.2s; }
.active {
  border-bottom: none; }
.dl {
  display: none;
  cursor: auto;
  text-align: left;
  border-bottom: 1px solid rgba(0,0,0, 0.1);
  &:hover {
    background-color: inherit; }
  td {
    padding: 5px 0 15px 15px; }
  li {
    display: inline-block;
    list-style-type: none;
    margin-right: 10px;
    a {
      color: white;
      background-color: #26c6da;
      text-decoration: none;
      border-radius: 20px;
      padding: 5px 15px;
      transition: background 0.1s;
      &:hover {
        background-color: lighten(#26c6da, 12%); } } } }
.th-no {
  width: 5%; }
.th-size {
  width: 10%; }
.th-sub {
  width: 15%; }
.td-title {
  text-align: left; }

#footer {
  font-size: 12px; }
